.backdropExperience {
  backdrop-filter: blur(5px);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 100vw;
  height: 100%;

  overflow-y: scroll;

  opacity: 1;
  visibility: visible;
  pointer-events: initial;

  background: linear-gradient(
    45deg,
    rgb(133, 133, 133) 2%,
    rgba(215, 217, 223, 0.213) 100%
  );

  @include mq(desktop) {
    overflow-y: hidden;
  }
}

.ishidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modalExperience {
  width: 100vw;
  font-size: 12px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  padding: 10px;
  @include mq(tablet) {
    padding: 20px;
    font-size: 16px;
    color: #fff;
  }

  @include mq(desktop) {
    font-size: 20px;
  }
}

.boxAboutMeThird {
  height: 100%;
  width: 100%;
  max-width: 400px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;

  background-color: var(--backgroundCube);
  border: var(--border);
  padding: 15px;

  @include mq(tablet) {
    max-width: 700px;
    padding: 20px;
  }
  @include mq(desktop) {
    // max-width: 1200px;
    padding: 30px;
  }
}

.workTitle {
  text-align: center;
  margin-bottom: 20px;
}

.workText {
  margin-bottom: 15px;
}

.modal__close-btnE {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 30px;
  height: 30px;
  position: absolute;
  top: 0;
  right: 0;
  border: var(--border);
  background-color: rgba(209, 207, 207, 0.8);

  z-index: 1000;

  @include mq(tablet) {
    border: var(--border);
    border-radius: 0;
  }
}

.modal__close-icon {
  fill: #fff;

  .modal__close-btnE:hover &,
  .modal__close-btnE:focus & {
    fill: black;
  }
}
